.timer {
  text-align: center;
  grid-template-columns: repeat(4, 1fr);
  gap: 40px;
  margin: 100px 200px;
  display: grid;
}

.field {
  flex-direction: column;
  display: flex;
}

.value {
  color: #fff;
  background-color: #000000d9;
  border-radius: 30px;
  width: 100%;
  height: 200px;
  font-size: 150px;
  transition: color .1s linear;
  box-shadow: 0 0 28px #000;
}

.value.changed {
  color: #0000;
  transition: color .1s linear;
  animation: .5s linear timeSwitch;
}

.label {
  margin-top: 20px;
  font-family: Courier New, Courier, monospace;
  font-size: 35px;
}

@keyframes timeSwitch {
  0% {
    transform: perspective(400px)rotateX(0);
  }

  100% {
    transform: perspective(400px)rotateX(180deg);
  }
}

